/* Infinite grid container */
.infinite-grid {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(90deg, #f7f7d4 1px, transparent 1px),
              linear-gradient(#f7f7d4 1px, transparent 1px);
  background-size: 50px 50px;
  cursor: grab;
  overflow: hidden;
}

/* Timeline container */
.timeline {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100000px; /* Extend horizontally for infinite effect */
  height: 2px;
  background: transparent;
  transform-origin: center;
}

/* Future line */
.line.future-line {
  position: absolute;
  top: -1px;
  height: 2px;
  width: 100%;
  background-color: gray;
}

/* Past line */
.line.past-line {
  position: absolute;
  top: 1px;
  height: 2px;
  width: 100%;
  background-color: cyan;
}
